.loading {
  width: 100%;
  height: 100%;
  min-height: 50vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.headerWrapper {
  display: flex;
  align-items: center;
  height: 300px;
  gap: 25px;
}
.header {
  margin-top: -40px;
  width: 100%;
  position: relative;
  &::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 300px;
    background: linear-gradient(to bottom right, #de626237, #ffb88c2e);
    top: 0;
  }
  .headImage {
    top: 0;
    position: absolute;
    width: 100%;
    height: 300px;
    object-fit: cover;
  }
}
.picture {
  width: 220px;
  height: 220px;
  z-index: 1;
  position: relative;
  object-fit: cover;
}
.informationTitle {
  z-index: 2;
  color: white;
  text-shadow: 2px 2px #ffb88c81;
  height: 220px;
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 7px;
}
.root {
  margin-top: 50px;
}
